/* @flow */

/**
 * Export functions that are needed from the asmcyrpto library. This way we can import all functions
 * that are needed to calculte the SHA256 of a file asyncronosly with one import:
 * `const { Sha256, bytes_to_hex } = await import('../utils/sha256')`
 */
export { Sha256 } from 'asmcrypto.js/dist_es5/hash/sha256/sha256'
export { bytes_to_hex } from 'asmcrypto.js/dist_es5/other/utils'
